package material

import kotlinx.html.BUTTON
import react.RBuilder
import react.dom.RDOMBuilder
import react.dom.div
import react.dom.i
import react.dom.span
import styled.css
import styled.styledButton

private var materialButtonRequired = false
fun requireMaterialButton() {
    if(!materialButtonRequired) {
        materialButtonRequired = true
        kotlinext.js.require("@material/react-button/dist/button.css")
    }
}

private fun RBuilder.materialBaseButton(
    _classes: String,
    label: String?,
    icon: String? = null,
    trailingIcon: String? = null,
    block: RDOMBuilder<BUTTON>.() -> Unit) =
    styledButton {
        css {
            classes.add(_classes)
        }
        div("mdc-button__ripple") {}
        icon?.let {
            i("material-icons mdc-button__icon") { +it }
        }
        label?.let {
            span("mdc-button__label") { +it }
        }
        trailingIcon?.let {
            i("material-icons mdc-button__icon") { +it }
        }
        block()
    }

fun RBuilder.materialTextButton(label: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button", label, null, null, block)
fun RBuilder.materialTextButton(label: String?, icon: String? = null, trailingIcon: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button", label, icon, trailingIcon, block)

fun RBuilder.materialOutlinedButton(label: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button mdc-button--outlined", label, null, null, block)
fun RBuilder.materialOutlinedButton(label: String?, icon: String? = null, trailingIcon: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button mdc-button--outlined", label, icon, trailingIcon, block)

fun RBuilder.materialContainedButton(label: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button mdc-button--raised", label, null, null, block)
fun RBuilder.materialContainedButton(label: String?, icon: String? = null, trailingIcon: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseButton("mdc-button mdc-button--raised", label, icon, trailingIcon, block)

private var materialIconButtonRequired = false
fun requireMaterialIconButton() {
    if(!materialIconButtonRequired) {
        materialIconButtonRequired = true
        kotlinext.js.require("@material/react-icon-button/dist/icon-button.css")
    }
}

private fun RBuilder.materialBaseIconButton(
    icon: String,
    icon_on: String? = null,
    block: RDOMBuilder<BUTTON>.() -> Unit) =
    styledButton {
        css {
            icon_on?.let {
                classes.add("mdc-icon-button toggle-button")
            } ?: classes.add("mdc-icon-button material-icons")
        }
        icon_on?.let {
            i("material-icons mdc-icon-button__icon mdc-icon-button__icon--on") { +icon_on }
            i("material-icons mdc-icon-button__icon") { +icon }
        } ?: +icon
        block()
    }

fun RBuilder.materialIconButton(icon: String, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseIconButton(icon, null, block)
fun RBuilder.materialIconButton(icon: String, icon_on: String?, block: RDOMBuilder<BUTTON>.() -> Unit) =
    materialBaseIconButton(icon, icon_on, block)
