package material

import kotlinx.css.*
import kotlinx.html.DIV
import kotlinx.html.id
import react.RBuilder
import react.RComponent
import react.RProps
import react.RState
import react.dom.RDOMBuilder
import react.dom.div
import react.dom.h3
import react.dom.p
import styled.css
import styled.styledDiv

class ButtonList: RComponent<RProps, RState>() {
    override fun RBuilder.render() {
        buttonContainer {
            materialTextButton("Text Button") {}
        }
        buttonContainer {
            materialTextButton("Icon Button", "bookmark", null) {}
        }
        buttonContainer {
            materialTextButton("Trailing Icon", null, "done") {}
        }
        p {}
        buttonContainer {
            materialOutlinedButton("Outlined Button") {}
        }
        buttonContainer {
            materialOutlinedButton("Icon Button", "bookmark", null) {}
        }
        buttonContainer {
            materialOutlinedButton("Trailing Icon", null, "done") {}
        }
        p {}
        buttonContainer {
            materialContainedButton("Contained Button") {}
        }
        buttonContainer {
            materialContainedButton("Icon Button", "bookmark", null) {}
        }
        buttonContainer {
            materialContainedButton("Trailing Icon", null, "done") {}
        }
        p {}
        buttonContainer {
            materialIconButton("done") {}
        }
        buttonContainer {
            materialIconButton("favorite_border", "favorite") {}
        }
        h3 {
            attrs.id = "contained-buttons"
            +"Contained Buttons"
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                }
                +"Default"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                }
                +"Primary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "secondary"
                }
                +"Secondary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    disabled = true
                }
                +"Disabled"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    href = "#contained-buttons"
                }
                +"Link"
            }
        }
        p {}
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "contained"
                    color = "primary"
                    disableElevation = true
                }
                +"Disable Elevation"
            }
        }
        h3 {
            attrs.id = "text-buttons"
            +"Text Buttons"
        }
        buttonContainer {
            MaterialButton { +"Default" }
        }
        buttonContainer {
            MaterialButton {
                attrs { color = "primary" }
                +"Primary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs { color = "secondary" }
                +"Secondary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs { disabled = true }
                +"Disabled"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    color = "primary"
                    href = "#text-buttons"
                }
                +"Link"
            }
        }
        h3 {
            attrs.id = "outlined-buttons"
            +"Outlined Buttons"
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                }
                +"Default"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                }
                +"Primary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "secondary"
                }
                +"Secondary"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    disabled = true
                }
                +"Disabled"
            }
        }
        buttonContainer {
            MaterialButton {
                attrs {
                    variant = "outlined"
                    color = "primary"
                    href = "#outlined-buttons"
                }
                +"Link"
            }
        }
    }
}

fun RBuilder.buttonContainer(block: RDOMBuilder<DIV>.() -> Unit) =
    styledDiv {
        css {
            boxSizing = BoxSizing.borderBox
            display = Display.inlineFlex
            padding(5.px)
        }
        block()
    }