import kotlinx.coroutines.MainScope
import kotlinx.coroutines.launch
import kotlinx.css.padding
import kotlinx.css.px
import material.ButtonList
import material.MDCIconButtonToggle
import material.requireMaterialButton
import material.requireMaterialIconButton
import org.w3c.dom.MediaQueryList
import org.w3c.dom.get
import react.*
import react.dom.h1
import react.dom.h2
import react.dom.header
import styled.css
import styled.styledDiv
import kotlin.browser.document
import kotlin.browser.window

external interface AppState: RState {
    var widthLevel: Int
}

class App: RComponent<RProps, AppState>() {
    override fun RBuilder.render() {
        requireMaterialButton()
        requireMaterialIconButton()
        header {
            h1 { +"Material Components" }
        }
        styledDiv {
            css {
                padding(5.px)
            }
            h2 { +"Buttons" }
            child(ButtonList::class) {}
            h2 { +"Cards" }
            //child(CardList::class) {}
        }
        MainScope().launch {
            val query = document.querySelectorAll(".toggle-button")
            for(i in 0 until query.length) {
                val node = query[i]
                node?.let { MDCIconButtonToggle(it.parentElement) }
            }
        }
    }
    override fun AppState.init() {
        fun mediaCheck(widthQuery: String, onTrue: Int, onFalse: Int): MediaQueryList {
            val c = window.matchMedia("only screen and ($widthQuery)")
            c.addListener {
                println("media Check ($widthQuery, ${if(c.matches) onTrue else onFalse})")
                setState {
                    widthLevel = if(c.matches) onTrue else onFalse
                } }
            return c
        }
        if (mediaCheck("max-width: 600px", 1, 2).matches) widthLevel = 1
        if (mediaCheck("min-width: 600px", 2, 1).matches) widthLevel = 2
        if (mediaCheck("min-width: 768px", 3, 2).matches) widthLevel = 3
        if (mediaCheck("min-width: 992px", 4, 3).matches) widthLevel = 4
        if (mediaCheck("min-width: 1200px", 6, 4).matches) widthLevel = 5
    }
}